<template>
  <el-container>
    <el-container>
      <el-header style="height: 30px;">
      </el-header>
      <el-main class="main-page">
        <post :title="title" :category="category"/>
      </el-main>
      <el-footer style="height: auto;" class="comment-out">
        <comment/>
      </el-footer>
    </el-container>
    <el-aside style="width: 30%;">
    </el-aside>
  </el-container>
</template>
<script>
import Post from './Post.vue'
import comment from '../comment'
export default {
  name: 'DesktopPost',
  components: { Post, comment },
  data () {
    return {
      category: undefined,
      title: undefined,
      treeData: [],
      rightSideWidth: '300px', // default
      currentKey: undefined,
      treeIdPosition: [],
      treeIdMap: {}
    }
  },
  watch: {
    currentKey: {
      // 监听key的变化值,注意该值和tree中的key并非一个值
      handler () {
        // 发生变化后,修改tree内部的key,将除了该节点之外的节点都收起
        this.$refs.tree.setCurrentKey(this.currentKey)
        const map = this.$refs.tree.store.nodesMap
        Object.keys(map).forEach(k => {
          map[k].expanded = k === this.currentKey
        })
        // 之后递归拿到所有的节点
        this.findParentIdBySubId(this.currentKey)
      }
    }
  },
  created () {
    this.category = this.$route.params.category
    this.title = this.$route.params.title
  }
}
</script>
<style>
html, body{
  scroll-behavior: smooth;
}
</style>



